.App {
    background-color: var(--dark-navy);
    display: flex;
    flex-direction: row;
}

html {
    scroll-behavior: smooth;
}

.section-header {
    display: flex;
    padding-bottom: 25px;
    width: 100vw;
}
.section-title {
    font-family: "NTR",sans-serif;
    color: var(--slate);
    font-size: 46px;
    font-weight: bold;
}

.fade-in-section {
    opacity: 0;
    transform: translateY(10vh);
    visibility: hidden;
    transition: opacity 0.6s ease-out, transform 1s ease-out;
    will-change: opacity, visibility;
}

.fade-in-section.is-visible {
    opacity: 1;
    transform: none;
    visibility: visible;
}